<script>
import { mapGetters } from 'vuex'
import constants from '@/util/constants'
import bus from '@/util/eventbus'

export default {
  name: 'bd-aside',
  data () {
    return {
      isCollapse: true,
      showSupport: constants.SHOW_SUPPORT,
      headerColor: '#3f3f3f'
    }
  },
  computed: {
    ...mapGetters([ 'userInfo', 'connectionStatus', 'showAuthority' ]),
    currentYear () {
      return new Date().getFullYear()
    },
    connecting () {
      return this.connectionStatus === constants.STOMP_CONNECTION_STATUS_CONNECTING
    },
    connected () {
      return this.connectionStatus === constants.STOMP_CONNECTION_STATUS_CONNECTED
    },
    disconnected () {
      return this.connectionStatus === constants.STOMP_CONNECTION_STATUS_DISCONNECTED
    },
    connectingImage () {
      return constants.CONNECTING_IMAGE
    },
    connectedImage () {
      return constants.CONNCTED_IMAGE
    },
    disconnectedImage () {
      return constants.DISCONNECTED_IMAGE
    }
  },
  methods: {
