      // console.log('close', key, keyPath)
    }
  },
  created () {
    let color = localStorage.headerColor
    // console.log('headerColor:', color)
    if (color !== null && color !== undefined) {
      this.headerColor = color
    }
  },
  mounted () {
    bus.$on('collapse', collapse => {
      this.isCollapse = collapse
    })
  }

}
