import Vue from 'vue'
import Router from 'vue-router'
import store from '@/store'

import Auth from '@/components/Auth'
import Dashboard from '@/components/Dashboard'

import BdLogin from '@/components/auth/BdLogin'
import BdRegister from '@/components/auth/BdRegister'
import BdForget from '@/components/auth/BdForget'

import BdChat from '@/components/dashboard/main/chat/BdChat'
import BdContact from '@/components/dashboard/main/contact/BdContact'
import BdFavorite from '@/components/dashboard/main/favorite/BdFavorite'

import BdSetting from '@/components/dashboard/setting/BdSetting'

import NotFound from '@/components/common/404'

import constants from '@/util/constants'

Vue.use(Router)

// 在管理后台页面，检查是否已经登录，如果没有登录，则跳转到/login
function requireAuth (to, from, next) {
  if (!store.getters.connected) {
    next({
      path: '/login',
      query: { redirect: to.fullPath }
    })
  } else {
    next()
  }
}

// 在登录界面和注册界面，检查是否已经登录，如果已经登录，则直接跳转/admin
function beforeAuth (to, from, next) {
  if (!store.getters.connected) {
    next()
  } else {
    next({
      path: '/admin',
      query: { redirect: to.fullPath }
    })
  }
}

const router = new Router({
  routes: [
    {
      path: '/',
      component: Auth,
      children: [
        {
          path: '',
          component: BdLogin,
          beforeEnter: beforeAuth,
          meta: { title: '登录 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'login',
          name: 'login', // 不能注释掉，route-link中有使用
          component: BdLogin,
          beforeEnter: beforeAuth,
          meta: { title: '登录 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'register',
          name: 'register',
          component: BdRegister,
          beforeEnter: beforeAuth,
          meta: { title: '注册 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'forget',
          name: 'forget',
          component: BdForget,
          beforeEnter: beforeAuth,
          meta: { title: '找回密码 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: '/logout',
          name: 'logout',
          beforeEnter (to, from, next) {
            store.dispatch('logout').then(() => {
              console.log('go to login page')
              next('/login')
            })
          }
        }
      ]
    },
    {
      path: '/admin',
      component: Dashboard,
      beforeEnter: requireAuth,
      children: [
        {
          path: '',
          component: BdChat,
          meta: { title: '对话列表 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'chat',
          name: 'chat',
          component: BdChat,
          meta: { title: '对话列表 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'contact',
          name: 'contact',
          component: BdContact,
          meta: { title: '联系人 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'favorite',
          name: 'favorite',
          component: BdFavorite,
          meta: { title: '我的收藏 - ' + constants.DEFAULT_TITLE }
        },
        {
          path: 'setting',
          name: 'setting',
          component: BdSetting,
          meta: { title: '我的设置 - ' + constants.DEFAULT_TITLE }
        }
      ]
    },
    {
      path: '*',
      component: NotFound
    }
  ]
})

router.beforeEach((to, from, next) => {
  // console.log('beforeEach to', to)
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

export default router
