<template>
  <div id='bd-aside'>
    <el-collapse v-model="activeNames" @change="handleChange">
      <el-collapse-item name="1">
        <template slot="title">
          <div style="margin-left: 20px;">
            新的朋友TODO
          </div>
        </template>
      </el-collapse-item>
      <el-collapse-item name="2">
        <template slot="title">
          <div style="margin-left: 20px;">
            {{ collapseGroupTitle }}
          </div>
        </template>
        <li v-for="group in userGroups" :key="group.gid" class="list-item" @click="selectGroup(group)">
          <div class="list-left">
            <img class="avatar" width="30" height="30" :alt="group.nickname" :src="group.avatar">
          </div>
          <div class="list-right">
            <div class="top">
              <span class="nickname">{{ group.nickname }}</span>
            </div>
            <div class="bottom">
              <span class="content">{{ group.description }}</span>
            </div>
          </div>
        </li>
      </el-collapse-item>
      <el-collapse-item name="3">
        <template slot="title">
          <div style="margin-left: 20px;">
            {{ collapseContactTitle }}
          </div>
        </template>
        <li v-for="contact in userContacts" :key="contact.uid" class="list-item" @click="selectContact(contact)">
          <div class="list-left">
            <img class="avatar" width="30" height="30" :alt="contact.nickname" :src="contact.avatar">
          </div>
          <div class="list-right">
            <div class="top">
              <span class="nickname">{{ contact.nickname }}</span>
            </div>
            <div class="bottom">
              <span class="content">{{ contact.description }}</span>
            </div>
          </div>
        </li>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import constants from '@/util/constants'

export default {
  name: 'bd-aside',
  data () {
    return {
      activeNames: ['3']
    }
  },
  computed: {
    ...mapGetters([ 'userContacts', 'userGroups' ]),
    collapseGroupTitle () {
      return '群聊 (' + this.userGroups.length + ')'
    },
    collapseContactTitle () {
      return '联系人 (' + this.userContacts.length + ')'
    }
  },
  methods: {
    ...mapActions([ 'updateChatType' ]),
    handleChange (val) {
      console.log(val)
    },
    selectGroup (group) {
      let payload = { type: constants.THREAD_TYPE_GROUP, object: group }
      this.updateChatType(payload)
    },
    selectContact (contact) {
      let payload = { type: constants.THREAD_TYPE_CONTACT, object: contact }
      this.updateChatType(payload)
    }
  }
}
</script>

