import constants from '@/util/constants'

export default {
  name: 'bd-main',
  data () {
    return {
      //
    }
  },
  computed: {
    ...mapGetters([ 'userCurrentChatType', 'userCurrentContact', 'userCurrentGroup' ]),
    isContact () {
      return this.userCurrentChatType === constants.THREAD_TYPE_CONTACT
    },
    isGroup () {
      return this.userCurrentChatType === constants.THREAD_TYPE_GROUP
    }
  },
  methods: {
    ...mapActions([]),
    startChat () {
      console.log('startChat')
    }
  }
}
</script>

