<template>
  <div id='bd-aside'>
    <div class="thread-search">
      <!-- TODO:远程搜索 -->
      <el-input style="width: 80%;" size="mini" placeholder="搜索" suffix-icon="el-icon-search" v-model="search" clearable></el-input>
      <el-button style="float: right;" size="mini" icon="el-icon-plus" circle @click="showCreateGroupDialog()"></el-button>
    </div>
    <div style="height: calc(100% - 100px); overflow-y: auto;">
      <li v-for="thread in filterdThreads" :key="thread.tid" class="list-item"
        :class="{ ' active': thread.tid === userCurrentThread.tid }"
        @click="selectThread(thread)">
        <div class="list-left">
          <img v-if="isTypeThread(thread)" class="avatar" width="40" height="40" :alt="thread.visitor.nickname" :src="thread.visitor.avatar">
          <img v-if="isTypeContact(thread)" class="avatar" width="40" height="40" :alt="thread.contact.nickname" :src="thread.contact.avatar">
          <img v-if="isTypeGroup(thread)" class="avatar" width="40" height="40" :alt="thread.group.nickname" :src="thread.group.avatar">
        </div>
        <div class="list-right">
          <div class="top">
            <span v-if="isTypeThread(thread)" class="nickname">{{ thread.visitor.nickname }}</span>
            <span v-if="isTypeContact(thread)" class="nickname">{{ thread.contact.nickname }}</span>
            <span v-if="isTypeGroup(thread)" class="nickname">{{ thread.group.nickname }}</span>
            <span class="timestamp">
              {{ thread.timestamp | time}}
              <span v-if="thread.unreadCount !== 0" class="unreadCount">{{ thread.unreadCount }}</span>
            </span>
          </div>
          <div class="bottom">
            <span class="content">{{ thread.content }}</span>
          </div>
        </div>
      </li>
    </div>
    <audio id="audioPlay" :src="soundUrl" hidden="true"/>
    <el-dialog
      title="创建群组"
      :visible.sync="createGroupDialogVisible"
      width="550px"
      center>
      <el-transfer
        filterable
        filter-placeholder="请输入姓名"
        v-model="selectedContacts"
        :data="contactData"
        :titles="['联系人', '群成员']">
      </el-transfer>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="createGroupDialogVisible = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="doCreateGroup">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import constants from '@/util/constants'

export default {
  name: 'bd-aside',
  data () {
    return {
      search: '',
      soundUrl: constants.SOUND_DINGDONG_URL,
      createGroupDialogVisible: false,
      //
      selectedContacts: []
    }
  },
  computed: {
    ...mapGetters([ 'userThreads', 'userCurrentThread', 'userContacts' ]),
    filterdThreads () {
      return this.userThreads.filter(thread => {
        if (thread.type === constants.THREAD_TYPE_THREAD) {
          return thread.visitor.nickname.includes(this.search) || thread.content.includes(this.search)
        } else if (thread.type === constants.THREAD_TYPE_CONTACT) {
          return thread.contact.nickname.includes(this.search) || thread.content.includes(this.search)
        } else if (thread.type === constants.THREAD_TYPE_GROUP) {
          return thread.group.nickname.includes(this.search) || thread.content.includes(this.search)
        }
      })
    },
    contactData () {
      const data = []
      this.userContacts.forEach((contact, index) => {
        data.push({
          label: contact.realName,
          key: contact.uid
        })
      })
      return data
    }
  },
  methods: {
    ...mapActions([ 'updateCurrentThread', 'updateContact', 'updateGroup', 'createGroup' ]),
    selectThread (thread) {
      this.updateCurrentThread(thread)
    },
    isTypeThread (thread) {
      return thread.type === constants.THREAD_TYPE_THREAD
    },
    isTypeContact (thread) {
      return thread.type === constants.THREAD_TYPE_CONTACT
    },
    isTypeGroup (thread) {
      return thread.type === constants.THREAD_TYPE_GROUP
    },
    showCreateGroupDialog () {
      this.createGroupDialogVisible = true
    },
    // 创建群组, 昵称组成：最多三个被选择人的realName
    doCreateGroup () {
      this.createGroupDialogVisible = false
      if (this.selectedContacts.length === 0) {
        return
      }
      let n = 0
      let nickname = this.userInfo.realName
      for (let i = 0; i < this.selectedContacts.length; i++) {
        const uid = this.selectedContacts[i]
        for (let j = 0; j < this.contacts.length; j++) {
          const contact = this.contacts[j]
          if (uid === contact.uid) {
            nickname = nickname + ',' + contact.realName
            n++
            if (n > 2) {
              break
            }
          }
        }
      }
      let payload = { nickname: nickname, selectedContacts: this.selectedContacts }
      this.createGroup(payload)
      this.selectedContacts = []
    }
  },
  filters: {
    // 将日期过滤为 hour:minutes
    time (value) {
      if (typeof value === 'string') {
        return moment(value).format('MM-DD HH:mm')
      } else {
        return value
      }
    }
  }
}
</script>

